// import React, { useEffect, useState } from "react";
import axios from "axios";
import React, { lazy, useEffect, useState } from "react";

import Title from "../Title/Title";
// import axios from "axios";
import DetailsModal from "./DetailsModal/DetailsModal";
// import DropDown from "./DropDown/DropDown";

const baseUrl = process.env.REACT_APP_BACKEND_URL;

// const ResultsLoad = lazy(() => import(''))

const Result = ({
  open,
  show,
  setOpen,
  loading,
  winner,
  showResults,
  round,
}) => {
  console.log(" ~ round", round)
  const [storeRound, setStoreRound] = useState([]);

  useEffect(() => {
    // const localValue = localStorage.getItem("maxValue");
    // setStoreRound(localValue);
    axios
      .post(`${baseUrl}/time`)
      .then((res) => {
        setStoreRound(res.data.data.nftBattel);
      })
      .catch((err) => console.log(err));
  }, []);

  return (
    <>
      <div>
        <Title>RESULTS</Title>
      </div>

      {round <= storeRound.length ? (
        ""
      ) : (
        <p className="text-red-500 text-2xl">Round {round} is not played yet.</p>
      )}

      {open && (
        <DetailsModal
          data={show}
          showRound={round}
          close={() => setOpen(false)}
        />
      )}

      {/* ====== Table Section Start */}
      <div className="container mx-auto my-20 pb-40">
        <table className="rwd-table">
          <tbody className="uppercase">
            <tr>
              <th>winner</th>
              <th></th>
              <th>Loser</th>
              <th>solana</th>
            </tr>


            {loading ? (
              <>
                <div className="flex justify-center py-10 min-w-full">
                  <div className="loader text-green-700 text-center"></div>
                </div>
              </>
            ) : round <= storeRound.length ? (
              winner.map((item) => (
                <tr key={item._id}>
                  <td data-th="Winner" className="text-[#63E136]">
                    {`Elemental #${item.candidate1.nftNumber}`}
                  </td>
                  <td data-th="vs">VS</td>
                  <td data-th="Elemental ID" className="text-[#FC3232]">
                    {`Elemental #${item.candidate2.nftNumber}`}
                  </td>
                  <td data-th="Solana" className="table-width">
                    <button
                      className="bg-transparent hover:bg-[#D9D9D9] font-semibold text-white py-2 px-4 border border-[#D9D9D9]hover:border-transparent rounded"
                      onClick={() => showResults(item)}
                    >
                      View Results
                    </button>
                  </td>
                </tr>
              ))
            ) : (
              <>
                <tr>
                  <td data-th="Winner">-</td>
                  <td data-th="vs">-</td>
                  <td data-th="Elemental ID">-</td>
                </tr>
              </>
            )}
          </tbody>
        </table>
      </div>
      {/* ====== Table Section End */}
      <div className="flex justify-end items-center container mx-auto mb-10">
        <ul className="inline-flex -space-x-px">
          <li>
            <a href="#!" className="py-2 px-3 leading-tight text-white ">
              1
            </a>
          </li>
          <li>
            <a href="#!" className="py-2 px-3 leading-tight text-white ">
              2
            </a>
          </li>
          <li>
            <a
              href="#!"
              aria-current="page"
              className="py-2 px-3 leading-tight text-white "
            >
              3
            </a>
          </li>
          <li>
            <a href="#!" className="py-2 px-3 leading-tight text-white ">
              4
            </a>
          </li>
          <li>
            <a href="#!" className="py-2 px-3 leading-tight text-white ">
              5
            </a>
          </li>
          <li>
            <a href="#!" className="py-2 px-3 leading-tight text-white ">
              Next
            </a>
          </li>
        </ul>
      </div>
    </>
  );
};

export default Result;
