import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import { useEffect, useState } from "react";
import Winner from "../Winner/Winner";
import axios from "axios";
import Title from "../Title/Title";

const baseUrl = process.env.REACT_APP_BACKEND_URL;

function WinnerDropDown() {
  const handleChange = (event) => {
    setRoundNumber(event.target.value);
  };

  const [winner, setWinner] = useState([]);

  const [show, setShow] = useState({});
  const [roundNumber, setRoundNumber] = useState(1);
  const [open, setOpen] = useState(false);

  const [loading, setLoading] = useState(true);

  const showResults = (data) => {
    setShow(data);
    setOpen(true);
  };

  useEffect(() => {
    axios
      .post(`${baseUrl}/Battelnft`, {
        roundNumber: roundNumber,
      })
      .then((res) => {
        setWinner(res.data.data.nftBattel.battels);
        setLoading(false);
      })
      .catch((err) => console.log(err));
  }, [roundNumber]);

  const [storeRound, setStoreRound] = useState([]);

  useEffect(() => {
    axios
      .post(`${baseUrl}/time`)
      .then((res) => {
        setStoreRound(res.data.data.nftBattel);
        setLoading(false);
      })
      .catch((err) => console.log(err));
  }, []);

  return (
    <>
      <div className="text-center text-white">
        <FormControl sx={{ m: 1, minWidth: 80 }} className="bg-white">
          <InputLabel id="demo-simple-select-autowidth-label" />
          <Select
            labelId="demo-simple-select-autowidth-label"
            id="demo-simple-select-autowidth"
            value={roundNumber}
            onChange={handleChange}
          >
            <MenuItem value={1}>Round 1</MenuItem>
            <MenuItem value={2}>Round 2</MenuItem>
            <MenuItem value={3}>Round 3</MenuItem>
            <MenuItem value={4}>Round 4</MenuItem>
            <MenuItem value={5}>Round 5</MenuItem>
            <MenuItem value={6}>Round 6</MenuItem>
            {/* <MenuItem value={7}>Round 7</MenuItem>
            <MenuItem value={8}>Round 8</MenuItem>
            <MenuItem value={9}>Round 9</MenuItem>
            <MenuItem value={10}>Round 10</MenuItem>
            <MenuItem value={11}>Round 11</MenuItem>
            <MenuItem value={12}>Round 12</MenuItem>
            <MenuItem value={13}>Round 13</MenuItem> */}
          </Select>
        </FormControl>

        <p className="text-xl my-2 flex justify-center items-center ">
          {/* {winner.length === 1 ? (
            <p> Final Winner : </p>
          ) : (
            <p> Total Winners : </p>
          )}
          {loading ? (
            <>
              <div className="flex justify-center py-10">
                <div className="loader text-green-700 text-center"></div>
              </div>
            </>
          ) : (
            <span className="text-2xl text-green-400 pl-1">
              {winner.length}
            </span>
          )} */}

          {loading ? (
            <>
              <div className="flex justify-center py-10">
                <div className="loader text-green-700 text-center"></div>
              </div>
            </>
          ) : roundNumber <= storeRound.length ? (
            <>
            {winner.length === 1 ? (
            <p> Final Winner : </p>
          ) : (
            <p> Total Winners : </p>
          )}
          <span className="text-2xl text-green-400 pl-1">{winner.length}</span></>
          ) : (
            <>
              <span>Round {roundNumber} is not played yet</span>
            </>
          )}
        </p>
        <Winner
          open={open}
          show={show}
          setOpen={setOpen}
          winner={winner}
          showResults={showResults}
          round={roundNumber}
        />
      </div>
    </>
  );
}

export default WinnerDropDown;
