import React, { lazy, Suspense } from "react";
import { Routes, Route } from "react-router";
// import Battle from "./components/Battle/Battle";
import Footer from "./components/Footer/Footer";

import Upgrade from './components/upgrade/Upgrade'

// components
import Header from "./components/Header/Header";
// eslint-disable-next-line
import Home from "./components/Home/Home";
import Play from "./components/Play/Play";
// import Result from "./components/Result/Result";
import DropDown from "./components/Result/DropDown/DropDown";
import Round from "./components/Round/Round";
import Table from "./components/Table/Table";
// import Winner from "./components/Winner/Winner";
import WinnerDropDown from "./components/WinnerDropDown/WinnerDropDown";
const LazyBattle = lazy(() => import("./components/Battle/Battle"));

const App = () => {
  return (
    <>
      <Header />
      <Suspense fallback={<div className="text-white"> Loading...</div>}>
        <Routes>
          <Route exact path="/" element={<Home />} />
          <Route exact path="/winner" element={<WinnerDropDown />} />
          <Route exact path="/result" element={<DropDown />} />

          <Route exact path="/battle" element={<LazyBattle />} />
          <Route exact path="/round" element={<Round />} />
          <Route exact path="/table" element={<Table />} />
          <Route exact path="/play" element={<Play />} />
          <Route exact path="/upgrade" element={<Upgrade/> } />
        </Routes>
      </Suspense>
      {/* <Footer /> */}
    </>
  );
};

export default App;
