import React, { useEffect, useState } from "react";
import axios from "axios";
import AdminLogin from "./AdminLogin/AdminLogin";

const baseUrl = process.env.REACT_APP_BACKEND_URL;

const Play = () => {
  const [roundNumber, setRoundNumber] = useState("");

  const [message, setMessage] = useState("");

  const [open, setopen] = useState(false);

  const [store, setStore] = useState([]);

  localStorage.setItem("maxValue", store.sort().slice(-1)[0]);

  const changeHandler = (e) => {
    setRoundNumber(e.target.value);
  };

  const playRound = () => {
    axios
      .post(`${baseUrl}/rumbleNfts`, { roundNumber: Number(roundNumber) })
      .then((res) => {
        console.log(" ~ .then ~ res", res);
        setMessage(res.data.message);
        // localStorage.setItem("roundNumber", roundNumber);
        // setStore((prevVal) => [
        //   ...prevVal,
        //   Number(localStorage.getItem("roundNumber")),
        // ]);
      })
      .catch((err) => console.log(err));
  };

  return (
    <>
      {/* <button
        className="text-white bg-red-600 px-3 py-1 rounded-md"
        onClick={() => setopen(true)}
      >
        Login
      </button> */}
      <div className="container mx-auto text-center text-white pb-16">
        {/* {open && <AdminLogin close={() => setopen(false)} />} */}
        <p className="">
          <input
            type="number"
            value={roundNumber}
            onChange={changeHandler}
            className="text-black py-1 px-2 placeholder:text-gray-400 w-48"
            placeholder="Enter round number"
            min="1"
            max="13"
          />
          <button
            className="bg-[#F10041] ml-4 py-2 px-6 rounded-md"
            onClick={playRound}
          >
            BATTLE
          </button>
        </p>
        <p className="mt-4 font-medium text-xl text-white">{message}</p>

        {/* <p>Upgraded Nfts : </p> */}
      </div>
    </>
  );
};

export default Play;
