import React, { useEffect, useState } from "react";
import axios from "axios";
import Title from "../Title/Title";

const baseUrl = process.env.REACT_APP_UPGRADE_URL;
console.log(" ~ baseUrl", baseUrl);

const Upgrade = () => {
  const [roundNumber, setRoundNumber] = useState("");

  const [message, setMessage] = useState("");

  const [open, setopen] = useState(false);

  const [store, setStore] = useState([]);

  localStorage.setItem("maxValue", store.sort().slice(-1)[0]);

  const changeHandler = (e) => {
    setRoundNumber(e.target.value);
  };

  const playRound = () => {
    axios
      .post(`${baseUrl}/updateNFT`, { roundNumber: Number(roundNumber) })
      .then((res) => {
        console.log(" ~ .then ~ res", res);
        setMessage(res.data.message);
      })
      .catch((err) => console.log(err));
  };

  return (
    <>
      <div className="container mx-auto text-center text-white pb-16">
        <div>
          <Title>Upgrade</Title>
        </div>
      </div>

      <div className="container mx-auto text-center text-white pb-16">
        {/* {open && <AdminLogin close={() => setopen(false)} />} */}
        <p className="">
          <input
            type="number"
            value={roundNumber}
            onChange={changeHandler}
            className="text-black py-1 px-2 placeholder:text-gray-400 w-48"
            placeholder="Enter round number"
            min="1"
            max="13"
          />
          <button
            className="bg-[#F10041] ml-4 py-2 px-6 rounded-md"
            onClick={playRound}
          >
            UPGRADE
          </button>
        </p>
        <p className="mt-4 font-medium text-xl text-white">{message}</p>

        {/* <p>Upgraded Nfts : </p> */}
      </div>
    </>
  );
};

export default Upgrade;
