import axios from "axios";
import React, { useEffect, useState } from "react";
import SolanaIcon from "../Assets/Images/Solana.png";
import Title from "../Title/Title";
const baseUrl = process.env.REACT_APP_BACKEND_URL;

const Winner = ({ open, show, setOpen, winner, showResults, round }) => {
    const [storeRound, setStoreRound] = useState([]);

  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // const localValue = localStorage.getItem("maxValue");
    // setStoreRound(localValue);
    axios
      .post(`${baseUrl}/time`)
      .then((res) => {
        setStoreRound(res.data.data.nftBattel);
        setLoading(false);
      })
      .catch((err) => console.log(err));
  }, []);

  return (
    <>
      <div>
        <Title>Winners</Title>
      </div>

      {/* ====== Table Section Start */}
      <div className="container mx-auto my-20 pb-40">
        <table className="rwd-table">
          <tbody className="uppercase">
            <tr>
              <th>winner</th>
              <th>Owner Wallet</th>
              <th>solana</th>
            </tr>

            {loading ? (
              <>
                <div className="flex justify-center py-10 min-w-full">
                  <div className="loader text-green-700 text-center"></div>
                </div>
              </>
            ) : round <= storeRound.length ? (
              winner.map((item) => (
                <tr key={item._id}>
                  <td data-th="Winner">{item.candidate1.nftNumber}</td>
                  {/* <td data-th="Elemental ID">
                      {item.candidate1.nftToken[0].owner_wallet}
                    </td> */}
                  <td>-</td>
                  <td data-th="Solana" className="table-width">
                    <div>
                      <img src={SolanaIcon} alt="solana" className="h-7" />
                    </div>
                    {item.solana}
                  </td>
                </tr>
              ))
            ) : (
              <>
                <tr>
                  <td data-th="Winner">-</td>
                  <td data-th="vs">-</td>
                  <td data-th="Elemental ID">-</td>
                </tr>
              </>
            )}
          </tbody>
        </table>
      </div>

      {/* ====== Table Section End */}
      <div className="flex justify-end items-center container mx-auto mb-10">
        <ul className="inline-flex -space-x-px">
          <li>
            <a href="#!" className="py-2 px-3 leading-tight text-white ">
              1
            </a>
          </li>
          <li>
            <a href="#!" className="py-2 px-3 leading-tight text-white ">
              2
            </a>
          </li>
          <li>
            <a
              href="#!"
              aria-current="page"
              className="py-2 px-3 leading-tight text-white "
            >
              3
            </a>
          </li>
          <li>
            <a href="#!" className="py-2 px-3 leading-tight text-white ">
              4
            </a>
          </li>
          <li>
            <a href="#!" className="py-2 px-3 leading-tight text-white ">
              5
            </a>
          </li>
          <li>
            <a href="#!" className="py-2 px-3 leading-tight text-white ">
              Next
            </a>
          </li>
        </ul>
      </div>
    </>
  );
};

export default Winner;
